* {
  margin: 0;
  padding: 0;
  font-family: "open sans";
}
.blockHouseLiquidityContainerr {
  background-color: #1e1e1e;
  overflow-x: hidden;
  background-image: url("./Images/liquidity/blockHouseBackground.png");
  background-size: cover;
  background-repeat: no-repeat;
  width: 100%;
  margin: 0 auto;
}
.insidetopHeader {
  margin-right: -35px;
}
.insidetopHeader > .siderBarNavItemRightBox {
  margin-left: 18px;
  display: inline-block;
  width: 36px;
  height: 36px;
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.12);
  background: rgba(60, 62, 75, 0.24);
}

.topHeaderWalletBox {
  width: 172px !important;
  padding-top: 7px;
}
.topHeaderWalletBox span {
  display: flex;
  justify-content: space-between;
}
.topHeaderWalletIcon {
  padding-left: 10px;
}
.topHeaderWalletDropdown {
  padding-right: 10px;
}
.topHeaderWalletBox > span > span {
  font-weight: bold;
  font-size: 14px;
  line-height: 20px;
  color: white;
  position: relative;
  top: 1px;
}

.siderBarLogo {
  width: 162px;
  height: 36px;
  position: relative;
  left: -60px;
}
.sideBarBoxItemsItemActive {
  background-color: rgba(255, 255, 255, 0.04) !important;
  /* border-top-right-radius: 8px !important; */
}
.sideBarBoxItemsItem {
  margin-top: 0px;
  width: 100%;
  max-height: 52px !important;
  min-height: 52px !important;
  padding-left: 20px !important;
  padding-right: 20px !important;
}
.tableHeadingTxt {
  letter-spacing: -1px;
  font-size: 30px !important;
  color: white;
}

.mainContainerSearchDiv span {
  position: relative;
  top: 35px;
  margin-top: 8px;
  margin-left: 8px;
}
.mainContainerSearchDiv input {
  width: 100%;
  height: 36px;
  background: rgba(255, 255, 255, 0.04);
  border-radius: 6px;
  border: none;
  padding-left: 40px;
  color: rgba(255, 255, 255, 0.6);
  outline: none;
}

.liquidityTableLastRowFlexLeftItem span {
  position: relative;
  top: -6px;
  height: 18px;
}
.width10Percent {
  width: 10% !important;
}
.customTxtForLiqTable {
  font-size: 15px;
  text-align: left;
  margin: 0;
  font-weight: bold;
}

.rightSearchBoxStructured {
  font-size: 14px;
  background: rgba(255, 255, 255, 0.04);
  border-radius: 6px;
  color: white;
  height: 38px;
  margin-top: 27px !important;
  padding-top: 6px;
  padding-left: 16px;
  font-weight: bold;
  width: 100%;
  text-align: start !important;
}
.rightSearchBoxStructured img {
  position: relative;
  right: -12px;
  top: 3px;
}

.liquidityTokenDropdownBox {
  width: 40px;
  height: 40px;
  border: double 1.7px transparent;
  border-radius: 6px;
  background-image: linear-gradient(95.25deg, #1b1d2e, #2a2e3a),
    linear-gradient(90deg, #141eed, #6094eb);
  background-origin: border-box;
  background-clip: content-box, border-box;
  margin-top: -10px;
}
.liquidityTokenDropdownBox img {
  padding-top: 8px;
  padding-left: 8px;
}
@media screen and (max-width: 1684px) {
  .inputSearchWidthMeida {
    max-width: 71.5% !important;
  }
}
@media screen and (max-width: 1600px) {
  .inputSearchWidthMeida {
    flex: 0 0 68% !important;
  }
  .mainContainerSearchDiv2 {
    flex: 0 0 10.5% !important;
    max-width: 10.5% !important;
  }
  .mainContainerSearchDiv3 {
    flex: 0 0 16.2% !important;
    max-width: 16.2% !important;
  }
}
@media screen and (max-width: 1500px) {
  .inputSearchWidthMeida {
    flex: 0 0 66% !important;
  }
  .mainContainerSearchDiv2 {
    flex: 0 0 11% !important;
    max-width: 11% !important;
  }
  .mainContainerSearchDiv3 {
    flex: 0 0 17.3% !important;
    max-width: 17.3% !important;
  }
}
@media screen and (max-width: 1408px) {
  .inputSearchWidthMeida {
    flex: 0 0 65% !important;
  }
  .rightSearchBoxStructured img {
    right: -5px;
    top: 7px;
  }
  .rightSearchBoxStructured {
    font-size: 13px;
    padding-top: 4px;
  }
}
@media screen and (max-width: 1540px) {
  .liqudityTableIconBox {
    width: 10% !important;
  }
}
@media screen and (max-width: 1421px) {
  .liqudityTableIconBox {
    width: 11% !important;
  }
  .liquidityTableLastArrow {
    left: 30px !important;
  }
}
@media screen and (max-width: 1324px) {
  .liqudityTableIconBox {
    width: 12% !important;
  }
}
@media screen and (max-width: 1243px) {
  .liqudityTableIconBox {
    width: 14% !important;
  }
}
